@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&family=Rubik:wght@300&display=swap');

.each-slide-effect > div {
  height: 90vh;
  background-size: cover;
  background-position: top;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}

.image__info {
  z-index: 200 !important;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  align-items: flex-end;
  width: 400px;
  margin-bottom: 40px;
  margin-right: 60px;
}

.image__quote,
.image__person {
  color: white;
  font-family: "Roboto";
  z-index: 205 !important;
  margin: 6px 0;
  text-align: end;
}

.image__quote {
  font-size: 56px;
  font-weight: 600;
}

.image__person {
  font-size: 32px;
  font-weight: 400;
}

.default-nav {
  background-color: transparent !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  border-radius: 0 !important;
  transition: all 300ms ease;
  margin: 0 12px;
  transform: scale(1.6);
}

.default-nav:hover {
  transform: scale(1.8);
}

.default-nav svg path {
  fill: white !important;
  transition: all 300ms ease;
}

.default-nav:hover svg path {
  fill: rgb(199, 199, 199) !important;
}