@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&family=Rubik:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&family=Rubik:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: "Roboto";
}

body {
  background-color: #050505;
  margin-bottom: 80px;
}
nav {
  height: 250px;
  width: 100%;
}

.nav--container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0 50px;
}

.header__logo--wrapper {
  width: 350px;
  margin: 0;
  margin-bottom: -20px;
}

.header__logo {
  width: 100%;
}

.header__links {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.header__link {
  color: rgb(194, 194, 194);
  font-size: 22px;
  padding: 4px 0;
  transition: all 300ms ease;
  text-decoration: none;
}

.header__link:hover {
  color: white;
  padding-right: 4px;
  cursor: pointer;
}

.header__burger-bar {
  color: white;
  -webkit-transform: scale(3);
          transform: scale(3);
  cursor: pointer;
  display: none;
}

.close__links-menu--btn,
.header__burger--btn {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

.links__menu {
  z-index: 1000;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #050505;
  display: none;
}

.close__links--icon {
  cursor: pointer;
  -webkit-transform: scale(5);
          transform: scale(5);
  position: absolute;
  right: 24px;
  top: 24px;
}

.menu__links {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.menu__link {
  color: white;
  font-size: 28px;
  font-weight: 500;
  padding: 24px 0;
  cursor: pointer;
  text-decoration: none;
}

.close__links--icon > path {
  stroke: white;
}

@media (max-width: 1200px) {
  nav {
    height: 230px;
  }

  .header__logo--wrapper {
    width: 300px;
  }
}

/* Small laptops, tablets, large smart phones */
@media (max-width: 868px) {
  nav {
    height: 200px;
  }

  .nav--container {
    padding: 0 24px;
  }

  .header__logo--wrapper {
    width: 250px;
  }
}

@media (max-width: 768px) {
  /* INSERT BUGER BAR AT THIS POINT */

  nav {
    height: 180px;
  }

  .header__link {
    display: none;
  }

  .header__burger-bar {
    display: block;
  }

  .header__logo--wrapper {
    width: 200px;
  }
}

/* Small phones */
@media (max-width: 480px) {
}

footer {
  background-color: white;
  width: 100%;
  padding: 18px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
  position: fixed;
  bottom: 0;
}

.footer__link {
  color: black;
}

.icon {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  padding: 0 18px;
  transition: all 250ms ease;
  cursor: pointer;
}

.icon:hover {
  fill: rgb(63, 63, 63);
}
.each-slide-effect > div {
  height: 90vh;
  background-size: cover;
  background-position: top;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}

.image__info {
  z-index: 200 !important;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  align-items: flex-end;
  width: 400px;
  margin-bottom: 40px;
  margin-right: 60px;
}

.image__quote,
.image__person {
  color: white;
  font-family: "Roboto";
  z-index: 205 !important;
  margin: 6px 0;
  text-align: end;
}

.image__quote {
  font-size: 56px;
  font-weight: 600;
}

.image__person {
  font-size: 32px;
  font-weight: 400;
}

.default-nav {
  background-color: transparent !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  border-radius: 0 !important;
  transition: all 300ms ease;
  margin: 0 12px;
  -webkit-transform: scale(1.6);
          transform: scale(1.6);
}

.default-nav:hover {
  -webkit-transform: scale(1.8);
          transform: scale(1.8);
}

.default-nav svg path {
  fill: white !important;
  transition: all 300ms ease;
}

.default-nav:hover svg path {
  fill: rgb(199, 199, 199) !important;
}
#videos {
  display: flex;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.videos__top--info {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 0;
}

.videos__top--para {
  color: white;
  font-size: 24px;
  width: 90%;
}

.videos__top--bold {
  font-size: 42px;
  font-weight: 500;
}

.videos--container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.video {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(14, 14, 14);
  width: 100%;
  max-width: 900px;
  padding: 12px 12px;
  margin: 12px 0;
  text-decoration: none;
  transition: all 500ms ease;
  border: none;
  cursor: pointer;
}

.video:hover {
  -webkit-transform: translateY(-8px);
          transform: translateY(-8px);
}

.video__img--wrapper {
  width: 330px;
  height: 100%;
  overflow: hidden;
  margin: 0;
  margin-right: 28px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.video__img {
  width: 100%;
}

.video__info {
  height: 100%;
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.video__info-top {
  display: flex;
  flex-direction: column;
}

.video__title {
  font-size: 36px;
  color: white;
  margin: 0;
}

.video__creator {
  color: rgb(219, 219, 219);
  padding-bottom: 20px;
  margin: 0;
  font-weight: 500;
}

.video__date {
  margin: 0;
  color: rgb(243, 26, 26);
  font-size: 20px;
  font-weight: 500;
}

.videos__see-more {
  color: rgb(194, 194, 194);
  text-align: center;
  cursor: pointer;
  font-weight: 400;
  transition: all 300ms ease;
  font-size: 16px;
  text-decoration: none;
}

.videos__see-more:hover {
  color: white;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

@media (max-width: 1200px) {
  .video {
    max-width: 700px;
    justify-content: center;
    align-items: center;
    padding: 12px 12px;
  }
}

@media (max-width: 800px) {
  .video {
    max-width: 600px;
  }

  .video__title {
    font-size: 30px;
  }

  .video__creator {
    font-size: 18px;
  }

  .video__date {
    font-size: 18px;
  }
}

@media (max-width: 630px) {
  .video {
    max-width: 400px;
  }

  .video__title {
    font-size: 24px;
  }

  .video__creator {
    font-size: 16px;
  }

  .video__date {
    font-size: 16px;
  }

  .video__img--wrapper {
    margin-right: 14px;
  }
}

@media (max-width: 484px) {
  .video {
    display: flex;
    flex-direction: column;
    padding: 12px 8px;
  }

  .video__img--wrapper {
    margin: 0;
    margin-bottom: 8px;
  }

  .video__info {
    width: 95%;
  }

  .video__title {
    width: 100%;
    font-size: 26px;
  }

  .video__creator {
    font-size: 20px;
  }

  .video__date {
    font-size: 18px;
  }
}

@media (max-width: 380px) {
  .video {
    max-width: 280px;
    padding: 8px 8px;
  }

  .video__img--wrapper {
    width: 100%;
  }

  .video__info {

  }
}
main:has(div):has(iframe) {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 1422px;
  margin: 0 auto;
}

main > div:has(iframe) {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

main > div > iframe {
  width: 100%;
  height: 500px;
}

.videos__list--container {
  width: 100%;
  height: -webkit-min-content;
  height: min-content;
  display: flex;
  margin: 0 auto;
  flex-flow: row-reverse wrap-reverse;
  justify-content: center;
  align-items: center;
  padding-top: 40px;
}

.video__item {
  width: 450px;
  display: flex;
  flex: 1 1 450px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 12px 12px;
  padding: 10px 12px;
  background-color: rgb(14, 14, 14);
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.video__item:last-child {
  background-color: #303030;
}

.video__item__img--wrapper {
  width: 100%;
  margin: 0;
  padding: 0;
  height: 220px;
  overflow: hidden;
  margin-top: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
}

.video__item__img {
  width: 100%;
}

.video__item__info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
  text-align: start;
}

.video__item__title,
.video__item__creator,
.video__item__date {
  padding: 0;
  margin: 0;
}

.video__item__title {
  color: white;
  font-size: 32px;
  padding-top: 8px;
}

.video__item__creator {
  color: #d6d6d6;
  padding-top: 4px;
}

.video__item__date {
  color: #f31a1a;
  font-weight: 400;
  padding-top: 22px;
}


@media (max-width: 730px) {
  main > div > iframe {
    height: 400px;
  }
}

@media (max-width: 480px) {
  main > div > iframe {
    height: 300px;
  }

  .video__item {
    height: 300px;
  }

  .video__item__title {
    font-size: 26px;
  }

  .video__item__creator {
    font-size: 16px;
  }

  .video__item__date {
    font-size: 16px;
  }
}
#contact {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
}

.contact--container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 360px;
  height: 100%
}

#contact__form {
  display: flex;
  flex-direction: column;
}

.form__item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
}

.form__item--label {
  color: #ffff;
  font-weight: 600;
  font-size: 24px;
}

.input {
  background-color: transparent;
  border: none;
  border-bottom: 3px solid #c0c4cc;
  color: #ffff;
  height: 40px;
  width: 100%;
  outline: none;
  padding: 0;
  transition: border 300ms ease;
}

.input:focus {
  border-bottom: 3px solid red;
}

textarea {
  min-width: 100%;
  max-width: 100%;
  max-height: 200px;
}

.form__submit {
  padding: 0;
  margin: 0;
  border: none;
  background-color: red;
  border: 2px solid red;
  color: white;
  font-weight: 600;
  font-size: 24px;
  padding: 14px 0;
  transition: all 300ms ease;
  cursor: pointer;
}

.form__submit:hover {
  border: 2px solid white;
  background-color: transparent;
}

.modal__overlay {
  display: none;
}
