#contact {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
}

.contact--container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 360px;
  height: 100%
}

#contact__form {
  display: flex;
  flex-direction: column;
}

.form__item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
}

.form__item--label {
  color: #ffff;
  font-weight: 600;
  font-size: 24px;
}

.input {
  background-color: transparent;
  border: none;
  border-bottom: 3px solid #c0c4cc;
  color: #ffff;
  height: 40px;
  width: 100%;
  outline: none;
  padding: 0;
  transition: border 300ms ease;
}

.input:focus {
  border-bottom: 3px solid red;
}

textarea {
  min-width: 100%;
  max-width: 100%;
  max-height: 200px;
}

.form__submit {
  padding: 0;
  margin: 0;
  border: none;
  background-color: red;
  border: 2px solid red;
  color: white;
  font-weight: 600;
  font-size: 24px;
  padding: 14px 0;
  transition: all 300ms ease;
  cursor: pointer;
}

.form__submit:hover {
  border: 2px solid white;
  background-color: transparent;
}

.modal__overlay {
  display: none;
}