main:has(div):has(iframe) {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 1422px;
  margin: 0 auto;
}

main > div:has(iframe) {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

main > div > iframe {
  width: 100%;
  height: 500px;
}

.videos__list--container {
  width: 100%;
  height: min-content;
  display: flex;
  margin: 0 auto;
  flex-flow: row-reverse wrap-reverse;
  justify-content: center;
  align-items: center;
  padding-top: 40px;
}

.video__item {
  width: 450px;
  display: flex;
  flex: 1 1 450px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 12px 12px;
  padding: 10px 12px;
  background-color: rgb(14, 14, 14);
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.video__item:last-child {
  background-color: #303030;
}

.video__item__img--wrapper {
  width: 100%;
  margin: 0;
  padding: 0;
  height: 220px;
  overflow: hidden;
  margin-top: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
}

.video__item__img {
  width: 100%;
}

.video__item__info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
  text-align: start;
}

.video__item__title,
.video__item__creator,
.video__item__date {
  padding: 0;
  margin: 0;
}

.video__item__title {
  color: white;
  font-size: 32px;
  padding-top: 8px;
}

.video__item__creator {
  color: #d6d6d6;
  padding-top: 4px;
}

.video__item__date {
  color: #f31a1a;
  font-weight: 400;
  padding-top: 22px;
}


@media (max-width: 730px) {
  main > div > iframe {
    height: 400px;
  }
}

@media (max-width: 480px) {
  main > div > iframe {
    height: 300px;
  }

  .video__item {
    height: 300px;
  }

  .video__item__title {
    font-size: 26px;
  }

  .video__item__creator {
    font-size: 16px;
  }

  .video__item__date {
    font-size: 16px;
  }
}